import { create } from 'zustand'
import { devtools, persist } from 'zustand/middleware'

export type MessageRole = 'user' | 'ai'

export interface CardMessage {
  id: string
  role: MessageRole
  content: string
  files?: string[]
  timestamp?: number
}

export interface CardData {
  id: string
  title: string
  messages: CardMessage[]
  position: [number, number, number]
  brightness: number
  parentId?: string
  children: string[]
  depth: number
}

interface CardStore {
  cards: CardData[]
  currentCardId: string | null
  selectedContent: string[]
  isTyping: boolean
  
  // Actions
  addCard: (messages?: CardMessage[], parentId?: string) => void
  appendMessage: (cardId: string, message: CardMessage) => void
  updateCard: (id: string, updates: Partial<CardData>) => void
  deleteCardAndDescendants: (id: string) => void
  setCurrentCard: (id: string | null) => void
  setSelectedContent: (content: string[]) => void
  setIsTyping: (typing: boolean) => void
  generateTitle: (id: string) => void
}

function getDescendantIds(cards: CardData[], id: string): string[] {
  const descendants: string[] = []
  function dfs(cardId: string) {
    cards.filter(c => c.parentId === cardId).forEach(child => {
      descendants.push(child.id)
      dfs(child.id)
    })
  }
  dfs(id)
  return descendants
}

function deduplicateMessages(messages: CardMessage[]): CardMessage[] {
  const seen = new Set<string>();
  return messages.filter(msg => {
    if (seen.has(msg.id)) return false;
    seen.add(msg.id);
    return true;
  });
}

function deduplicateCards(cards: CardData[]): CardData[] {
  const seen = new Set<string>();
  return cards.map(card => ({
    ...card,
    position: [0, 100, 0] as [number, number, number],
    messages: deduplicateMessages(card.messages),
    children: card.children || [] // 确保children字段存在
  })).filter(card => {
    if (seen.has(card.id)) return false;
    seen.add(card.id);
    return true;
  });
}

export const useCardStore = create<CardStore>()(
  devtools(
    persist(
      (set, get) => ({
        cards: [],
        currentCardId: null,
        selectedContent: [],
        isTyping: false,

        addCard: (messages = [], parentId) => {
          const newCard: CardData = {
            id: `card_${Date.now()}`,
            title: '新卡片',
            messages,
            position: [0, 100, 0],
            brightness: 1.0,
            parentId,
            children: [],
            depth: parentId ? (get().cards.find(c => c.id === parentId)?.depth || 0) + 1 : 0
          }

          set((state) => ({
            cards: deduplicateCards([...state.cards, newCard]),
            currentCardId: newCard.id
          }))

          // 更新父卡片的children
          if (parentId) {
            set((state) => ({
              cards: deduplicateCards(state.cards.map(card =>
                card.id === parentId
                  ? { ...card, children: [...(card.children || []), newCard.id] }
                  : card
              ))
            }))
          }
        },

        appendMessage: (cardId, message) => {
          set((state) => ({
            cards: deduplicateCards(state.cards.map(card => {
              if (card.id !== cardId) return card;
              // 检查是否已存在同id消息
              const idx = card.messages.findIndex(m => m.id === message.id);
              if (idx !== -1) {
                // 已存在则只更新内容
                const newMessages = [...card.messages];
                newMessages[idx] = { ...newMessages[idx], ...message };
                return { ...card, messages: deduplicateMessages(newMessages) };
              } else {
                // 不存在则追加
                return { ...card, messages: deduplicateMessages([...card.messages, message]) };
              }
            }))
          }))
        },

        updateCard: (id, updates) => {
          set((state) => ({
            cards: state.cards.map(card =>
              card.id === id ? { ...card, ...updates } : card
            )
          }))
        },

        deleteCardAndDescendants: (id) => {
          set((state) => {
            const idsToDelete = [id, ...getDescendantIds(state.cards, id)]
            return {
              cards: state.cards.filter(card => !idsToDelete.includes(card.id)),
              currentCardId: idsToDelete.includes(state.currentCardId!) ? null : state.currentCardId
            }
          })
        },

        setCurrentCard: (id) => {
          set({ currentCardId: id })
        },

        setSelectedContent: (content) => {
          set({ selectedContent: content })
        },

        setIsTyping: (typing) => {
          set({ isTyping: typing })
        },

        generateTitle: async (id) => {
          const card = get().cards.find(c => c.id === id)
          if (!card) return
          // 简单的标题生成逻辑
          const lastMsg = card.messages.length > 0 ? card.messages[card.messages.length - 1].content : ''
          const title = lastMsg.length > 10 ? lastMsg.substring(0, 10) + '...' : lastMsg || '新卡片'
          get().updateCard(id, { title })
        }
      }),
      {
        name: 'ai-card-tree-storage',
        partialize: (state) => ({
          cards: deduplicateCards(state.cards),
          currentCardId: state.currentCardId
        }),
        // 每次rehydrate时自动去重
        onRehydrateStorage: () => (state) => {
          if (state && state.cards) {
            state.cards = deduplicateCards(state.cards);
          }
        }
      }
    ),
    {
      name: 'card-store'
    }
  )
) 