import { Component, ErrorInfo, ReactNode } from 'react'
import { ProjectPanel } from './components/ProjectPanel'
import { CardStack } from './components/CardStack'
import { TreeNavigation } from './components/TreeNavigation'
import { InputArea } from './components/InputArea'

// 错误边界组件
interface ErrorBoundaryState {
  hasError: boolean
  error?: Error
}

class ErrorBoundary extends Component<{ children: ReactNode }, ErrorBoundaryState> {
  constructor(props: { children: ReactNode }) {
    super(props)
    this.state = { hasError: false }
  }

  static getDerivedStateFromError(error: Error): ErrorBoundaryState {
    return { hasError: true, error }
  }

  componentDidCatch(error: Error, errorInfo: ErrorInfo) {
    console.error('React错误边界捕获到错误:', error, errorInfo)
  }

  render() {
    if (this.state.hasError) {
      return (
        <div className="h-screen flex items-center justify-center bg-dark-bg text-white">
          <div className="text-center p-8">
            <h1 className="text-2xl font-bold mb-4">应用出现错误</h1>
            <p className="text-gray-400 mb-4">请刷新页面重试</p>
            <button
              onClick={() => window.location.reload()}
              className="px-4 py-2 bg-neon-green text-black rounded-lg font-medium"
            >
              刷新页面
            </button>
            {this.state.error && (
              <details className="mt-4 text-left">
                <summary className="cursor-pointer text-gray-400">错误详情</summary>
                <pre className="mt-2 p-4 bg-gray-800 rounded text-sm overflow-auto">
                  {this.state.error.toString()}
                </pre>
              </details>
            )}
          </div>
        </div>
      )
    }

    return this.props.children
  }
}

function App() {
  return (
    <ErrorBoundary>
      <div className="h-screen flex bg-dark-bg">
        {/* 项目管理面板 */}
        <div className="w-64 bg-card-bg border-r border-gray-700">
          <ProjectPanel />
        </div>
        
        {/* 主内容区域 */}
        <div className="flex-1 flex flex-col h-full overflow-hidden relative">
          {/* 卡片堆叠区域 - 使用flex-1确保占据剩余空间 */}
          <div className="flex-1 relative overflow-hidden">
            <CardStack />
          </div>
          
          {/* 输入区域 */}
          <InputArea />
        </div>
        
        {/* 树状导航 */}
        <div className="w-80 bg-card-bg border-l border-gray-700">
          <TreeNavigation />
        </div>
      </div>
    </ErrorBoundary>
  )
}

export default App 