import React, { useRef, useState, useEffect } from 'react'
import { useCardStore, CardData, CardMessage } from '../stores/cardStore'
import { motion } from 'framer-motion'
import { Trash2 } from 'lucide-react'

// 新增：多段文本选中逻辑
const useMultiTextSelection = () => {
  const { selectedContent, setSelectedContent } = useCardStore()
  // 记录已选文本，避免重复
  const addSelection = (text: string) => {
    if (text && !selectedContent.includes(text)) {
      setSelectedContent([...selectedContent, text])
    }
  }
  const clearSelection = () => setSelectedContent([])
  return { selectedContent, addSelection, clearSelection }
}

const MessageBubble: React.FC<{ msg: CardMessage }> = ({ msg }) => {
  const isUser = msg.role === 'user'
  const { addSelection } = useMultiTextSelection()
  const [showBtn, setShowBtn] = useState(false)
  const bubbleRef = useRef<HTMLDivElement>(null)

  // 鼠标松开时检测选中文本
  const handleMouseUp = (e: React.MouseEvent) => {
    const selection = window.getSelection()
    if (selection && selection.toString().trim()) {
      // 支持Ctrl多选
      if (e.ctrlKey || e.metaKey) {
        addSelection(selection.toString().trim())
        setShowBtn(false)
      } else {
        // 单选时清空再选
        addSelection(selection.toString().trim())
        setShowBtn(true)
      }
      selection.removeAllRanges()
    }
  }

  return (
    <div className={`flex ${isUser ? 'justify-end' : 'justify-start'} mb-2`}>
      <div
        ref={bubbleRef}
        className={`max-w-[70%] px-4 py-2 rounded-2xl shadow text-sm whitespace-pre-line break-words select-text relative ${
          isUser
            ? 'bg-neon-green text-black rounded-br-md'
            : 'bg-gray-700 text-white rounded-bl-md'
        }`}
        style={{ wordBreak: 'break-word' }}
        onMouseUp={handleMouseUp}
      >
        {msg.content}
        {/* 新建卡片按钮，仅在单选时显示 */}
        {showBtn && (
          <button
            className="absolute right-2 top-2 bg-neon-green text-black px-2 py-1 rounded text-xs shadow hover:bg-green-400 z-30"
            onClick={() => {
              // 触发全局事件，由父组件处理动画和新卡片创建
              window.dispatchEvent(new CustomEvent('create-card-from-selection'))
              setShowBtn(false)
            }}
          >
            新建卡片
          </button>
        )}
      </div>
    </div>
  )
}

// 当前卡片对话框组件
const CurrentCardDialog: React.FC<{ card: CardData; onDelete: (id: string) => void }> = ({ 
  card, 
  onDelete 
}) => {
  return (
    <motion.div
      className="w-[36rem] md:w-[40rem] h-[30rem] md:h-[36rem] bg-card-bg border border-gray-700 rounded-2xl shadow-2xl flex flex-col z-10"
      initial={{ scale: 0.9, opacity: 0 }}
      animate={{ scale: 1, opacity: 1 }}
      transition={{ duration: 0.3 }}
    >
      {/* 标题栏 */}
      <div className="flex items-center justify-between px-4 py-3 border-b border-gray-700 rounded-t-2xl">
        <div className="font-semibold text-white text-base truncate max-w-[360px] md:max-w-[440px]">
          {card.title}
        </div>
        <button
          className="p-1 hover:bg-gray-700 rounded transition-colors"
          title="删除当前及其所有子孙卡片"
          onClick={() => onDelete(card.id)}
        >
          <Trash2 className="w-5 h-5 text-gray-400 hover:text-red-500" />
        </button>
      </div>
      
      {/* 消息列表 */}
      <div className="flex-1 overflow-y-auto p-4 space-y-1 scrollbar-thin scrollbar-thumb-neon-green/60 scrollbar-track-gray-800">
        {card.messages.length === 0 ? (
          <div className="text-gray-500 text-center py-8">
            开始新的对话...
          </div>
        ) : (
          card.messages.map(msg => (
            <MessageBubble key={msg.id} msg={msg} />
          ))
        )}
      </div>
    </motion.div>
  )
}

export const CardStack: React.FC = () => {
  const { 
    cards, 
    currentCardId, 
    setCurrentCard, 
    deleteCardAndDescendants, 
    addCard, 
    selectedContent, 
    setSelectedContent 
  } = useCardStore()

  // 监听从选中文本创建卡片的事件
  useEffect(() => {
    const handleCreateCardFromSelection = () => {
      if (selectedContent.length > 0 && currentCardId) {
        // 创建包含选中内容的新卡片
        const newMessages: CardMessage[] = selectedContent.map((content, index) => ({
          id: `selected_${Date.now()}_${index}`,
          role: 'user' as const,
          content: content,
          timestamp: Date.now()
        }))
        
        addCard(newMessages, currentCardId)
        setSelectedContent([]) // 清空选中内容
      }
    }

    window.addEventListener('create-card-from-selection', handleCreateCardFromSelection)
    return () => {
      window.removeEventListener('create-card-from-selection', handleCreateCardFromSelection)
    }
  }, [selectedContent, currentCardId, addCard, setSelectedContent])

  // 获取当前卡片和父卡片
  const currentCard = cards.find(card => card.id === currentCardId)
  const parentCard = currentCard && currentCard.parentId
    ? cards.find(card => card.id === currentCard.parentId)
    : null

  // 删除卡片后切换到父卡片
  const handleDeleteCard = (id: string) => {
    const card = cards.find(c => c.id === id);
    const parentId = card?.parentId || null;
    deleteCardAndDescendants(id);
    setCurrentCard(parentId);
  };

  // 如果没有当前卡片，自动创建第一个卡片
  useEffect(() => {
    if (!currentCard && cards.length === 0) {
      console.log('自动创建第一个卡片')
      addCard([], undefined)
    }
  }, [currentCard, cards.length, addCard])

  // 调试信息
  useEffect(() => {
    console.log('CardStack状态:', {
      cardsCount: cards.length,
      currentCardId,
      currentCard: currentCard?.id,
      hasCurrentCard: !!currentCard
    })
  }, [cards.length, currentCardId, currentCard])

  // 如果没有当前卡片，渲染欢迎界面
  if (!currentCard) {
    return (
      <div className="flex justify-center items-center h-full w-full">
        <div className="text-center text-gray-500">
          <div className="text-2xl mb-4">📝</div>
          <div className="text-lg mb-2">欢迎使用AI对话卡片树</div>
          <div className="text-sm">正在创建您的第一个卡片...</div>
        </div>
      </div>
    )
  }

  // 计算同层卡片（同一parentId、同一depth）
  const sameLayerCards = cards.filter(
    c => c.parentId === currentCard.parentId && c.depth === currentCard.depth
  )
  // 按创建顺序排序，当前卡片排在最后
  const sortedLayerCards = sameLayerCards
    .filter(c => c.id !== currentCard.id)
    .concat(currentCard)

  // 正n边形排列参数
  const n = sortedLayerCards.length
  const radius = 120 + Math.max(0, n - 3) * 30 // 卡片环形半径，卡片多时自动增大
  const centerIndex = n - 1 // 当前卡片在最后

  // 卡片渲染顺序：先渲染后方卡片，再渲染当前卡片，保证遮挡
  return (
    <div className="flex justify-center items-center h-full w-full flex-1 relative">
      {/* 父卡片装饰性显示 */}
      {parentCard && (
        <motion.div
          className="absolute z-10"
          initial={{
            scale: 0.92,
            rotate: -8,
            x: -40,
            y: 40,
            opacity: 0.7,
            filter: 'brightness(0.6) blur(1px)'
          }}
          animate={{
            scale: 0.92,
            rotate: -8,
            x: -40,
            y: 40,
            opacity: 0.7,
            filter: 'brightness(0.6) blur(1px)'
          }}
          transition={{ duration: 0.3, type: 'spring', bounce: 0.18 }}
          style={{ pointerEvents: 'none' }}
        >
          <CurrentCardDialog card={parentCard} onDelete={() => {}} />
        </motion.div>
      )}
      {/* 主卡片flex居中渲染 */}
      {currentCard && (
        <div className="z-20 flex justify-center">
          <CurrentCardDialog
            card={currentCard}
            onDelete={handleDeleteCard}
          />
        </div>
      )}
      {/* 其他同层卡片：正n边形分布，边缘淡出/半透明/阴影 */}
      {sortedLayerCards.map((card, i) => {
        if (i === centerIndex) return null // 主卡片单独渲染
        const angle = (2 * Math.PI * i) / (n - 1)
        const x = Math.sin(angle) * radius
        const y = -Math.cos(angle) * radius
        const fade = 0.5 + 0.5 * (1 - Math.abs(i - centerIndex) / (n - 1))
        return (
          <motion.div
            key={card.id}
            className="absolute left-1/2 top-1/2 md:max-w-[calc(100vw-32rem)]"
            style={{
              transform: `translate(-50%, -50%) translate(${x}px, ${y}px) rotate(${angle * 30 - 15}deg)`,
              zIndex: 10 + i,
              pointerEvents: 'auto',
              filter: `blur(${Math.abs(i - centerIndex) > 1 ? 1.5 : 0}px)`,
              maxWidth: 'calc(100vw - 2rem)'
            }}
            initial={{ scale: 0.92, opacity: 0.7 }}
            animate={{ scale: 0.92, opacity: fade }}
            transition={{ duration: 0.35, type: 'spring', bounce: 0.22 }}
            whileHover={i !== centerIndex ? { scale: 1.1, boxShadow: '0 0 16px #00FF7F', zIndex: 99 } : {}}
            onClick={() => setCurrentCard(card.id)}
          >
            <CurrentCardDialog card={card} onDelete={() => {}} />
          </motion.div>
        )
      })}
    </div>
  )
} 