import React, { useState } from 'react'
import { Folder, Plus, Settings, Download, Upload, MessageSquare } from 'lucide-react'
import { useCardStore } from '../stores/cardStore'

export const ProjectPanel: React.FC = () => {
  const { addCard, currentCardId } = useCardStore()
  
  const [projects, setProjects] = useState([
    { id: '1', name: '默认项目', active: true },
    { id: '2', name: '学习笔记', active: false },
    { id: '3', name: '工作文档', active: false }
  ])

  const [showNewProject, setShowNewProject] = useState(false)
  const [newProjectName, setNewProjectName] = useState('')

  const handleCreateCard = () => {
    addCard([], currentCardId || undefined) // 创建新卡片，当前卡片作为父卡片
  }

  const handleCreateProject = () => {
    if (newProjectName.trim()) {
      const newProject = {
        id: Date.now().toString(),
        name: newProjectName,
        active: false
      }
      setProjects(prev => prev.map(p => ({ ...p, active: false })).concat(newProject))
      setNewProjectName('')
      setShowNewProject(false)
    }
  }

  const handleSwitchProject = (id: string) => {
    setProjects(prev => prev.map(p => ({ ...p, active: p.id === id })))
  }

  return (
    <div className="h-full flex flex-col">
      {/* 标题栏 */}
      <div className="p-4 border-b border-gray-700">
        <h2 className="text-lg font-semibold text-white">项目管理</h2>
      </div>

      {/* 项目列表 */}
      <div className="flex-1 overflow-y-auto p-4">
        <div className="space-y-2">
          {projects.map(project => (
            <div
              key={project.id}
              onClick={() => handleSwitchProject(project.id)}
              className={`p-3 rounded-lg cursor-pointer transition-colors ${
                project.active
                  ? 'bg-neon-green bg-opacity-20 border border-neon-green'
                  : 'bg-gray-800 hover:bg-gray-700'
              }`}
            >
              <div className="flex items-center space-x-3">
                <Folder className="w-5 h-5 text-gray-400" />
                <span className="text-sm font-medium">{project.name}</span>
              </div>
            </div>
          ))}
        </div>

        {/* 新建项目 */}
        {showNewProject && (
          <div className="mt-4 p-3 bg-gray-800 rounded-lg">
            <input
              type="text"
              value={newProjectName}
              onChange={(e) => setNewProjectName(e.target.value)}
              placeholder="项目名称"
              className="w-full p-2 bg-gray-700 rounded border border-gray-600 text-white text-sm"
              onKeyPress={(e) => e.key === 'Enter' && handleCreateProject()}
              autoFocus
            />
            <div className="flex space-x-2 mt-2">
              <button
                onClick={handleCreateProject}
                className="px-3 py-1 bg-neon-green text-black rounded text-sm font-medium"
              >
                创建
              </button>
              <button
                onClick={() => setShowNewProject(false)}
                className="px-3 py-1 bg-gray-600 text-white rounded text-sm"
              >
                取消
              </button>
            </div>
          </div>
        )}
      </div>

      {/* 底部操作栏 */}
      <div className="p-4 border-t border-gray-700">
        <div className="space-y-2">
          <button
            onClick={handleCreateCard}
            className="w-full flex items-center justify-center space-x-2 p-2 bg-neon-green text-black rounded font-medium"
          >
            <MessageSquare className="w-4 h-4" />
            <span>新建卡片</span>
          </button>
          
          <button
            onClick={() => setShowNewProject(true)}
            className="w-full flex items-center justify-center space-x-2 p-2 bg-gray-700 hover:bg-gray-600 rounded text-sm"
          >
            <Plus className="w-4 h-4" />
            <span>新建项目</span>
          </button>
          
          <div className="grid grid-cols-2 gap-2">
            <button className="flex items-center justify-center space-x-1 p-2 bg-gray-700 hover:bg-gray-600 rounded text-sm">
              <Upload className="w-4 h-4" />
              <span>导入</span>
            </button>
            <button className="flex items-center justify-center space-x-1 p-2 bg-gray-700 hover:bg-gray-600 rounded text-sm">
              <Download className="w-4 h-4" />
              <span>导出</span>
            </button>
          </div>
          
          <button className="w-full flex items-center justify-center space-x-2 p-2 bg-gray-700 hover:bg-gray-600 rounded text-sm">
            <Settings className="w-4 h-4" />
            <span>设置</span>
          </button>
        </div>
      </div>
    </div>
  )
} 