import React, { useRef, useState } from 'react'
import { Canvas, useFrame } from '@react-three/fiber'
import { OrbitControls } from '@react-three/drei'
import * as THREE from 'three'
import { useCardStore, CardData } from '../stores/cardStore'

interface TreeNodeProps {
  card: CardData
  depth: number
  onClick: () => void
  allCards: CardData[]
}

const TreeNode: React.FC<TreeNodeProps> = ({ card, depth, onClick, allCards }) => {
  const meshRef = useRef<THREE.Mesh>(null)
  const [hovered, setHovered] = useState(false)
  
  // 计算亮度
  const brightness = Math.max(0.2, 1 - (depth * 0.2))
  
  // 计算位置
  const x = depth * 100
  const y = 0
  const z = 0
  
  // 获取子节点
  const children = allCards.filter(c => c.parentId === card.id)
  
  useFrame(() => {
    if (meshRef.current) {
      if (hovered) {
        meshRef.current.scale.lerp(new THREE.Vector3(1.2, 1.2, 1.2), 0.1)
      } else {
        meshRef.current.scale.lerp(new THREE.Vector3(1, 1, 1), 0.1)
      }
    }
  })

  return (
    <group position={[x, y, z]}>
      {/* 卡片节点 */}
      <mesh
        ref={meshRef}
        onClick={onClick}
        onPointerOver={() => setHovered(true)}
        onPointerOut={() => setHovered(false)}
      >
        <boxGeometry args={[60, 80, 2]} />
        <meshStandardMaterial
          color={hovered ? '#00FF7F' : new THREE.Color(brightness, brightness, brightness)}
          emissive={hovered ? '#00FF7F' : '#000000'}
          emissiveIntensity={hovered ? 0.5 : 0}
          transparent={brightness < 0.6}
          opacity={Math.max(0.4, brightness)}
        />
      </mesh>
      
      {/* 连接线 */}
      {children.map((child) => {
        const childX = (depth + 1) * 100
        const childY = 0
        const childZ = 0
        
        return (
          <line key={child.id}>
            <bufferGeometry>
              <bufferAttribute
                attach="attributes-position"
                count={2}
                array={new Float32Array([
                  30, 0, 0,  // 从当前卡片右侧
                  childX - x - 30, childY - y, childZ - z  // 到子卡片左侧
                ])}
                itemSize={3}
              />
            </bufferGeometry>
            <lineBasicMaterial color="#00FF7F" opacity={0.8} transparent />
          </line>
        )
      })}
      
      {/* 子节点 */}
      {children.map((child) => (
        <TreeNode
          key={child.id}
          card={child}
          depth={depth + 1}
          onClick={() => {}} // 这里需要设置点击处理
          allCards={allCards}
        />
      ))}
    </group>
  )
}

const TreeScene: React.FC = () => {
  const { cards, setCurrentCard } = useCardStore()
  
  // 构建树结构
  const buildTree = (cards: CardData[]) => {
    const cardMap = new Map(cards.map(card => [card.id, card]))
    const rootCards: CardData[] = []
    
    cards.forEach(card => {
      if (card.parentId) {
        const parent = cardMap.get(card.parentId)
        if (parent) {
          parent.children.push(card.id)
        }
      } else {
        rootCards.push(card)
      }
    })
    
    return rootCards
  }
  
  const rootCards = buildTree(cards)
  
  return (
    <>
      {/* 环境光 */}
      <ambientLight intensity={0.3} />
      
      {/* 方向光 */}
      <directionalLight
        position={[0, 200, 100]}
        intensity={1.0}
      />
      
      {/* 树结构 */}
      <group position={[0, 0, 0]}>
        {rootCards.map(card => (
          <TreeNode
            key={card.id}
            card={card}
            depth={0}
            onClick={() => setCurrentCard(card.id)}
            allCards={cards}
          />
        ))}
      </group>
      
      {/* 控制器 */}
      <OrbitControls
        enablePan={false}
        enableZoom={true}
        enableRotate={true}
        maxPolarAngle={Math.PI / 2}
        minPolarAngle={0}
      />
    </>
  )
}

// 2D树结构小卡片
const MiniCard: React.FC<{
  card: CardData
  isActive: boolean
  isCurrent: boolean
  opacity: number
  onClick: () => void
  onHover: (show: boolean, card: CardData) => void
}> = ({ card, isActive, isCurrent, opacity, onClick, onHover }) => (
  <div
    className={`w-12 h-16 rounded bg-gray-400 border-2 flex items-center justify-center text-xs font-bold cursor-pointer transition-all duration-200 select-none ${
      isCurrent
        ? 'border-neon-green bg-white text-black shadow-lg scale-110 z-20'
        : isActive
        ? 'border-neon-green bg-gray-200 text-black shadow'
        : 'border-gray-600 bg-gray-700 text-gray-400'
    }`}
    style={{ opacity, boxShadow: isCurrent ? '0 0 12px #00FF7F' : undefined }}
    onClick={onClick}
    onMouseEnter={() => onHover(true, card)}
    onMouseLeave={() => onHover(false, card)}
    title={card.title}
  >
    {card.title || '卡片'}
  </div>
)

// 递归渲染树结构（防止环和死循环，限制最大深度）
const renderTree = (
  node: CardData,
  layers: CardData[][],
  currentLayer: number,
  centerLayer: number,
  setCurrentCard: (id: string) => void,
  currentCardId: string,
  setActiveId: (id: string | null) => void,
  activeId: string | null,
  visited: Set<string>,
  maxDepth = 12
) => {
  if (visited.has(node.id) || currentLayer > maxDepth) return null
  visited.add(node.id)
  const opacity = Math.max(0.3, 1 - Math.abs(currentLayer - centerLayer) * 0.22)
  const isCurrent = node.id === currentCardId
  const isActive = node.id === activeId
  return (
    <div key={node.id} style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
      <MiniCard
        card={node}
        isActive={isActive}
        isCurrent={isCurrent}
        opacity={opacity}
        onClick={() => setCurrentCard(node.id)}
        onHover={(show) => setActiveId(show ? node.id : null)}
      />
      {/* 子节点横向排列 */}
      {node.children && node.children.length > 0 && (
        <div style={{ display: 'flex', flexDirection: 'row', gap: 12, marginTop: 8 }}>
          {node.children.map(cid => {
            const child = layers[currentLayer + 1]?.find(c => c.id === cid)
            return child ? renderTree(child, layers, currentLayer + 1, centerLayer, setCurrentCard, currentCardId, setActiveId, activeId, visited, maxDepth) : null
          })}
        </div>
      )}
    </div>
  )
}

export const TreeNavigation: React.FC = () => {
  const { cards, setCurrentCard, currentCardId } = useCardStore()
  const [scroll, setScroll] = useState(0) // 层级滚动
  const [activeId, setActiveId] = useState<string | null>(null)
  const [show3D, setShow3D] = useState(false) // 控制3D/2D切换

  // 构建层级分组（不修改原始children）
  const layers: CardData[][] = []
  cards.forEach(card => {
    if (!layers[card.depth]) layers[card.depth] = []
    layers[card.depth].push(card)
  })
  // 当前卡片层级居中
  const current = cards.find(c => c.id === currentCardId)
  const centerLayer = current ? current.depth - scroll : 0
  // 根节点
  const roots = layers[0] || []

  // 滚轮事件
  const handleWheel = (e: React.WheelEvent) => {
    if (e.shiftKey) return // 预留旋转
    setScroll(s => Math.max(0, Math.min(layers.length - 1, s + (e.deltaY > 0 ? 1 : -1))))
  }

  return (
    <div className="h-full flex flex-col">
      {/* 标题栏 */}
      <div className="p-4 border-b border-gray-700">
        <div className="flex items-center justify-between">
          <h2 className="text-lg font-semibold text-white">树状导航</h2>
          <button
            onClick={() => setShow3D(!show3D)}
            className="px-2 py-1 bg-gray-700 hover:bg-gray-600 rounded text-xs"
          >
            {show3D ? '2D' : '3D'}
          </button>
        </div>
      </div>
      
      {/* 3D树结构 */}
      {show3D && (
        <div className="flex-1">
          <Canvas
            camera={{
              position: [0, 300, 0],
              rotation: [-Math.PI / 6, 0, 0], // -30°俯角
              near: 0.1,
              far: 2000
            }}
          >
            <TreeScene />
          </Canvas>
        </div>
      )}
      
      {/* 2D树结构 */}
      {!show3D && (
        <div className="flex-1 relative overflow-auto" onWheel={handleWheel} style={{ background: '#181818' }}>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', marginTop: 32, gap: 24 }}>
            {roots.map(root => renderTree(root, layers, 0, centerLayer, setCurrentCard, currentCardId || '', setActiveId, activeId, new Set()))}
          </div>
        </div>
      )}
      
      {/* 操作提示 */}
      <div className="p-4 border-t border-gray-700">
        <div className="text-sm text-gray-400 space-y-1">
          <p>• 鼠标滚轮：纵向浏览</p>
          <p>• Shift+滚轮：旋转视角（预留）</p>
          <p>• 悬停显示高亮，点击切换卡片</p>
        </div>
      </div>
    </div>
  )
} 