import React, { useState, useRef } from 'react'
import { Send, Upload, FileText, Image, X } from 'lucide-react'
import { useCardStore } from '../stores/cardStore'
import type { CardMessage } from '../stores/cardStore'

interface FilePreview {
  id: string
  name: string
  type: string
  url: string
}

// 从环境变量获取API配置，如果没有则使用默认值
const SILICONFLOW_API_URL = import.meta.env.VITE_SILICONFLOW_API_URL || 'https://api.siliconflow.cn/v1/chat/completions'
const DEFAULT_API_KEY = import.meta.env.VITE_DEFAULT_API_KEY || ''

export const InputArea: React.FC = () => {
  const { currentCardId, cards, addCard, appendMessage, setIsTyping, selectedContent, setSelectedContent } = useCardStore()
  const [inputText, setInputText] = useState('')
  const [filePreviews, setFilePreviews] = useState<FilePreview[]>([])
  const [isTyping, setTyping] = useState(false)
  const [apiKey, setApiKey] = useState(DEFAULT_API_KEY)
  const [showApiKeyInput, setShowApiKeyInput] = useState(!DEFAULT_API_KEY)
  const fileInputRef = useRef<HTMLInputElement>(null)

  const currentCard = cards.find(card => card.id === currentCardId)

  // 发送消息
  const handleSend = async () => {
    if (!inputText.trim() && filePreviews.length === 0) return
    
    // 检查API密钥
    if (!apiKey.trim()) {
      setShowApiKeyInput(true)
      return
    }
    
    try {
      setTyping(true)
      setIsTyping(true)
      
      const userMsgId = `msg_${Date.now()}_${Math.random().toString(36).slice(2)}`;
      const userMsg: CardMessage = {
        id: userMsgId,
        role: 'user',
        content: inputText,
        files: filePreviews.map(f => f.name),
        timestamp: Date.now()
      }
      
      // 当前卡片存在则追加消息，否则新建卡片
      let cardId = currentCardId
      if (cardId && currentCard) {
        appendMessage(cardId, userMsg)
        console.log('追加消息到现有卡片:', cardId, userMsg)
      } else {
        addCard([userMsg])
        cardId = useCardStore.getState().currentCardId!
        console.log('新建卡片:', cardId, userMsg)
      }
      
      setInputText('')
      setFilePreviews([])
      
      // 调用硅基流动API
      if (inputText.trim()) {
        await fetchLLMStream(cardId, inputText, userMsgId)
      }
    } catch (error) {
      console.error('发送消息时出错:', error)
      alert('发送消息失败，请重试')
    } finally {
      setTyping(false)
      setIsTyping(false)
    }
  }

  // 调用硅基流动API并流式追加AI消息
  const fetchLLMStream = async (cardId: string, prompt: string, userMsgId?: string) => {
    // AI消息id基于userMsgId生成，确保唯一
    const aiMsgId = userMsgId ? `${userMsgId}_ai` : `msg_${Date.now()}_${Math.random().toString(36).slice(2)}_ai`;
    let aiContent = ''
    
    try {
      // 每次流式追加都检查AI消息是否已存在，存在则只更新内容，不存在才插入
      const updateOrInsertAIMessage = (cardId: string, aiMsgId: string, aiContent: string) => {
        const currentCard = useCardStore.getState().cards.find(c => c.id === cardId);
        const aiMsgExists = currentCard && currentCard.messages.some(m => m.id === aiMsgId);
        if (aiMsgExists) {
          useCardStore.getState().appendMessage(cardId, {
            id: aiMsgId,
            role: 'ai',
            content: aiContent,
            timestamp: Date.now()
          });
        } else {
          appendMessage(cardId, {
            id: aiMsgId,
            role: 'ai',
            content: aiContent,
            timestamp: Date.now()
          });
        }
      };

      // 先插入一个空AI消息用于流式追加（仅当不存在时插入）
      updateOrInsertAIMessage(cardId, aiMsgId, '');
      
      const controller = new AbortController()
      
      const res = await fetch(SILICONFLOW_API_URL, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${apiKey}`
        },
        body: JSON.stringify({
          model: 'deepseek-ai/DeepSeek-R1-0528-Qwen3-8B',
          stream: true,
          messages: [{ role: 'user', content: prompt }]
        }),
        signal: controller.signal
      })
      
      if (!res.ok) {
        throw new Error(`API请求失败: ${res.status} ${res.statusText}`)
      }
      
      if (!res.body) {
        throw new Error('No response body')
      }
      
      const reader = res.body.getReader()
      let done = false
      
      while (!done) {
        const { value, done: doneReading } = await reader.read()
        done = doneReading
        
        if (value) {
          const chunk = new TextDecoder().decode(value)
          // 解析OpenAI兼容流式数据
          chunk.split('data:').forEach(part => {
            const str = part.trim()
            if (!str || str === '[DONE]') return
            
            try {
              const json = JSON.parse(str)
              const delta = json.choices?.[0]?.delta?.content
              if (delta) {
                aiContent += delta
                updateOrInsertAIMessage(cardId, aiMsgId, aiContent);
                console.log('AI消息流式追加:', aiContent)
              }
            } catch (parseError) {
              console.warn('解析流式数据时出错:', parseError)
            }
          })
        }
      }
      // 不再用updateCard批量替换messages数组，避免重复key
      // 只用appendMessage单点更新即可
      console.log('AI消息最终内容:', aiContent)
      
    } catch (error) {
      console.error('LLM API调用失败:', error)
      // 错误处理 - 更新AI消息为错误提示
      const currentCards = useCardStore.getState().cards
      const updatedMessages = currentCards.find(c => c.id === cardId)?.messages.map(m =>
        m.id === aiMsgId ? { ...m, content: 'AI回复失败，请检查API Key或网络连接。' } : m
      ) || []
      useCardStore.getState().updateCard(cardId, {
        messages: updatedMessages
      })
      // 兜底：如果AI消息没显示，也手动追加一条
      if (!updatedMessages.length) {
        appendMessage(cardId, {
          id: aiMsgId + '_err',
          role: 'ai',
          content: 'AI回复失败，请检查API Key或网络连接。',
          timestamp: Date.now()
        })
      }
    }
  }

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = event.target.files
    if (!files) return
    Array.from(files).forEach(file => {
      const reader = new FileReader()
      reader.onload = (e) => {
        const preview: FilePreview = {
          id: Date.now().toString(),
          name: file.name,
          type: file.type,
          url: e.target?.result as string
        }
        setFilePreviews(prev => [...prev, preview])
      }
      reader.readAsDataURL(file)
    })
  }

  const removeFile = (id: string) => {
    setFilePreviews(prev => prev.filter(f => f.id !== id))
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault()
      handleSend()
    }
  }

  const hasPreview = selectedContent.length > 0 || filePreviews.length > 0;
  return (
    <>
      {/* API密钥弹窗 */}
      {showApiKeyInput && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60">
          <div className="p-6 bg-gray-800 rounded-lg border border-gray-600 w-full max-w-md shadow-2xl">
            <div className="text-sm text-gray-300 mb-2">请输入您的API密钥：</div>
            <input
              type="password"
              value={apiKey}
              onChange={(e) => setApiKey(e.target.value)}
              placeholder="sk-..."
              className="w-full p-2 bg-gray-700 rounded border border-gray-600 text-white text-sm mb-2"
              onKeyPress={(e) => e.key === 'Enter' && setShowApiKeyInput(false)}
            />
            <div className="flex space-x-2">
              <button
                onClick={() => setShowApiKeyInput(false)}
                className="px-3 py-1 bg-neon-green text-black rounded text-sm font-medium"
              >
                确定
              </button>
              <button
                onClick={() => setShowApiKeyInput(false)}
                className="px-3 py-1 bg-gray-600 text-white rounded text-sm"
              >
                取消
              </button>
            </div>
          </div>
        </div>
      )}
      <div className="relative h-full w-full">
        {/* 预览区浮在输入UI上方，内容自适应高度，超出时滚动 */}
        {hasPreview && (
          <div
            className="absolute left-0 w-full px-4"
            style={{ bottom: '6.5rem' }} // 6.5rem为输入UI高度+间距
          >
            <div className="flex flex-row gap-2 items-start overflow-x-auto flex-nowrap min-h-[48px] max-h-[160px] pb-1 scrollbar-thin scrollbar-thumb-neon-green/60 scrollbar-track-gray-800">
              {/* 选中文本 */}
              {selectedContent.map((content, index) => (
                <div key={index} className="flex items-center bg-gray-800 rounded-lg px-3 py-2 space-x-2 min-w-max">
                  <span className="text-sm text-gray-300 bg-gray-700 p-2 rounded max-w-xs break-words">{content}</span>
                  <button
                    onClick={() => setSelectedContent(selectedContent.filter((_, i) => i !== index))}
                    className="text-gray-400 hover:text-red-400"
                    title="移除该文本"
                  >
                    <X className="w-4 h-4" />
                  </button>
                </div>
              ))}
              {/* 文件预览 */}
              {filePreviews.map(file => (
                <div key={file.id} className="flex items-center bg-gray-800 rounded-lg px-3 py-2 space-x-2 min-w-max">
                  {file.type.startsWith('image/') ? (
                    <Image className="w-4 h-4 text-blue-400" />
                  ) : (
                    <FileText className="w-4 h-4 text-green-400" />
                  )}
                  <span className="text-sm text-gray-300 max-w-xs break-words">{file.name}</span>
                  <button
                    onClick={() => removeFile(file.id)}
                    className="text-gray-400 hover:text-red-400"
                    title="移除该文件"
                  >
                    <X className="w-4 h-4" />
                  </button>
                </div>
              ))}
            </div>
          </div>
        )}
        {/* 输入UI绝对定位贴底 */}
        <div className="absolute bottom-0 left-0 w-full p-4 bg-card-bg z-0">
          <div className="flex flex-col">
            <div className="relative mb-4">
              <textarea
                value={inputText}
                onChange={(e) => setInputText(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder="输入您的问题..."
                className="w-full h-20 p-4 bg-gray-800 border border-gray-700 rounded-lg text-white resize-none focus:outline-none focus:border-neon-green"
                disabled={isTyping}
              />
            </div>
            {/* 底部控制栏 */}
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <button
                  onClick={() => fileInputRef.current?.click()}
                  className="p-2 bg-gray-700 hover:bg-gray-600 rounded-lg transition-colors"
                  title="上传文件"
                >
                  <Upload className="w-5 h-5 text-gray-400" />
                </button>
                <input
                  ref={fileInputRef}
                  type="file"
                  multiple
                  onChange={handleFileUpload}
                  className="hidden"
                />
                <button
                  onClick={() => setShowApiKeyInput(true)}
                  className="px-3 py-2 bg-gray-700 hover:bg-gray-600 rounded-lg text-sm transition-colors"
                  title="设置API密钥"
                >
                  API设置
                </button>
              </div>
              
              <button
                onClick={handleSend}
                disabled={isTyping || (!inputText.trim() && filePreviews.length === 0)}
                className="flex items-center space-x-2 px-4 py-2 bg-neon-green text-black rounded-lg font-medium hover:bg-green-400 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                <Send className="w-4 h-4" />
                <span>{isTyping ? '发送中...' : '发送'}</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  )
} 